import controls from './controls';

class YoutubeAPILoader {
	constructor() {
		this.KEY = 'AIzaSyCouAADWroSP9SX23a5KMzQlVa6X46fLJ4';
		this.URL = 'https://www.googleapis.com/youtube/v3/';
		this.maxResults = 15;
		this.prevPage = null;
		this.nextPage = null;
		this.searchRequest = null;
		this.statisticRequest = null;
		this.listOfVideos = [];
		this.totalResults = null;
	}

	makeSearchURL(purpose, part, type, q) {
		this.searchRequest = `${this.URL + purpose}?key=${this.KEY}&part=${part}&type=${type}&maxResults=${this.maxResults}&q=${q}`;
		return this.searchRequest;
	}

	makeStatisticURL(purpose, part, ids) {
		this.statisticRequest = `${this.URL + purpose}?key=${this.KEY}&part=${part}&id=${ids}`;
		return this.statisticRequest;
	}

	makeRequest(searchRequestUrl, callback) {
		const that = this;
		const requestSnippetList = [];
		fetch(searchRequestUrl)
			.then(response => response.json()).then((data) => {
				requestSnippetList.push(...data.items);
				that.nextPage = data.nextPageToken;
				that.totalResults = data.pageInfo.totalResults;
				const ids = that.getListOfVidiosId(requestSnippetList);
				const url = that.makeStatisticURL('videos', 'statistics', ids);
				return fetch(url);
			}).then(response => response.json())
			.then((data) => {
				that.addResponsDataToList(requestSnippetList, data.items);
				if (callback) {
					controls.responseDrower(callback);
				}
			})
			.catch(() => {

			});
	}

	getListOfVidiosId(responsList) {
		return responsList.map(item => item.id.videoId);
	}

	addResponsDataToList(snippetList, statisticList) {
		snippetList.forEach((item, index) => {
			const elem = item;
			elem.statistics = statisticList[index].statistics;
		});
		this.listOfVideos.push(...snippetList);
	}

	makePrevPageURL() {
		return `${this.searchRequest}&pageToken=${this.prevPage}`;
	}

	makeNextPageURL() {
		return `${this.searchRequest}&pageToken=${this.nextPage}`;
	}

	getSetLength() {
		return this.listOfVideos.length;
	}
}

export default new YoutubeAPILoader();
